.container {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-form {
  text-align: center;
}

.form-control {
  margin: 20px 0;
}

.search-btn {
  margin-left: 8px;
}

.show-more-btn {
  margin: 20px auto;
  display: block;
}

.articles {
  width: 1400px;
  gap: 10px;
  align-items: top;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.article__link {
  color: currentColor;
  text-decoration: none;
}

.article__item {
  width: 265px;
  align-items: center;
  justify-content: top;
  flex-direction: column;
  padding: 0 15px;
  display: flex;
  box-shadow: 6px 6px 15px rgba(0, 0, 0, .2);
}

.article__img {
  margin-bottom: 8px;
  display: block;
}

.article__title {
  margin: 0 0 8px;
  padding: 0;
  font-size: 25px;
}

.article__author {
  margin: 0 0 8px;
  padding: 0;
}

.article__description {
  margin: 0;
  padding: 0;
}

